
.SignUp {
  background-color: whitesmoke;
  text-align: center;
  margin: 0 auto;
}
.SignUpInputs input::placeholder {
  color: #23233c;
  font-size: 14px;
}
.SignUpInputs input {
  padding-left: 50px;
  width: 200px;
  height: 55px;
  border-radius: 5px;
  border: none;
  margin-bottom: 35px;
  box-shadow: 0px 5px 10px rgba(126, 126, 126, 0.3);
}

.SignUpContainer {
  height: auto;
  max-width: auto;
}

.SignUpHeader {
  background-color: whitesmoke;
  height: 170px;
  max-width: auto;
  background-image: url('../../assets/MainHeader.png');
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
}

.SignUpButton button {
  background-color: #101022;
  color: #fff;
  border: none;
  width: 225px;
  height: 60px;
  border-radius: 18px;
  font-size: 20px;
  font-weight: 700;
}

.SignUpButton button:hover {
  cursor: pointer;
}

.SignUpLink {
  color: #898888;
  margin-top: 20px;
}

.SignUpLink a {
  color: #898888;
  text-decoration: none;
  font-weight: 700;
}

.SignUpLink a:hover {
  color: #898888;

  text-decoration: none;
  cursor: pointer;
}

.SignUpTitle {
  color: #23233c;
  font-size: 40px;
  margin-top: 0px;
}

.SignUpFooter {
  background-color: whitesmoke;
  height: 170px;
  max-width: auto;
  background-image: url('../../assets/Footer.png');
  background-position: center;

  background-repeat: no-repeat;
  background-size: cover;
}
